* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 18px;
  text-align: justify;
  line-height: 1.6;
}

html, body {
    min-height: 100vh;
}
div, header, nav, main, section, footer {
  height: auto;
  overflow: auto;
}
  
header, footer {
  background-color: #4b5559;
}
  
main {
  background-color: #6d8282;
}

main section {
  width: 80%;
  margin: 20px auto 40px auto;
}

section#mainContent h2 {
  margin-top: 60px;
  margin-bottom: 40px;
  text-align: center;
  font-size: 30px;
  color: #E1E8EE;
}

section#mainContent h3 {
  margin: 5px;
  font-size: 25px;
  color: #E1E8EE;
  text-align: center;
}

section#mainContent button {
  display: block;
  margin: 30px auto 0px auto;
  padding: 10px 30px;
  text-transform: uppercase;
  text-decoration: none !important;
  font-weight: bold;
  background-color: #82a7b8;
  color: #3d4145;
  border: 2px solid #3d4145;
  cursor: pointer;
  -webkit-transition: 1.5s;
  transition: 1.5s;
}

section#mainContent button:hover {
  background-color: #3b89ad;
  color:white;
  -webkit-transition: 1.5s;
  transition: 1.5s;
}

section#mainContent table {
  margin: 30px auto 0px auto;
  border-spacing: 10px;
  text-transform: uppercase;
}

section#mainContent table tr td {
  background-color: #E1E8EE;
  padding: 10px 30px;
  vertical-align: middle;
}

section#mainContent table tr:first-child td {
  text-align: center;
  font-weight: bold;
}

section#mainContent table tr td:first-child, section#mainContent table tr td:last-child {
  text-align: center;
}

section#mainContent form {
  margin: 30px auto;
  text-align: center;
  font-weight: bold;
  color: #E1E8EE;
  line-height: 2.5;
}

div#pageTitle {
    display: flex;
    width: 90%;
    margin: 0 auto;
    padding: 100px 0px 50px 0px;
}

div#pageTitle h1 {
    color: #E1E8EE;
    margin: 0 auto;
    font-size: 40px;
}
header nav {
    background-color: #82a7b8;
    display: flex;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 1;
}

header nav ul {
    width: 100%;
    float: left;
    list-style-type: none;
}

header nav ul li {
    display: block;
    width: 33.33%;
    float: left;
    text-align: center;
    font-weight: bold;
    background-color: #82a7b8;
}

header nav ul li a {
    font-size: 16px;
    display: block;
    padding: 10px 20px;
    text-decoration: none;
    color: #3d4145;
    -webkit-transition: 1.5s;
    transition: 1.5s;
    text-align: center;
}

header nav ul li a.active {
    color: white;
}

header nav ul li a:not(.active):hover {
    background-color: #3b89ad;
    color:white;
    -webkit-transition: 1.5s;
    transition: 1.5s;
}

footer {
    padding: 5px;
    text-align: center;
    font-size: 14px;
    color: white;
}

footer span {
    font-size: 14px;
    font-style: italic;
    color: white;
}
